text {
    font-family: AppleSDGothicNeo;
    font-size: 1rem;
    font-weight: 800;
    text-align: left;
    color: #151515;
    
}
.container{
    border-top : 0.063rem solid #ededed;
    padding-top : 4.063rem;
}

.itemBox{
    display : flex;
    flex-direction: row;
    margin-bottom : 3.75rem;
 
}

.insideContainer{
    border: 0.1em solid;
    width : 100%;
    height : 7.5em;
    margin-right : 1.5em;
    border-color: #d5d5d5;

    display: flex;
    flex-direction: column;
}

.insideContainerRow{
    border: 0.1em solid;
    width : 100%;
    height : 7.5em;
    margin-right : 1.5em;
    border-color: #d5d5d5;

    display: flex;
    flex-direction: row;
}


.infoBox{
    height : 3.75rem;
    border-bottom: 0.1em solid;
    border-color: #d5d5d5;
    display : flex;
    flex-direction: row;
}

.infoBoxNoBorder{
    height : 3.75rem;
    margin : auto 0rem aut0 aut0;
    justify-content: right;
    display : flex;
    flex-direction: row;
   
}

.titleText{
    font-family: acumin-pro-wide, sans-serif;
    font-weight: 700;
    font-size : 0.75rem;
    font-style: normal;
    text-align: left;
    color: #2b2b2b;
    margin-bottom: 2.25rem;
    margin : auto auto auto 0rem;

}

.textRank {
    text-align: right;
    font-family: AppleSDGothicNeo;
    font-size: 5em;
    font-weight: bold;
    color: #00462a;
    text-align: right;
    margin : auto 1rem auto auto;
}
.subText{
   
  font-family: AppleSDGothicNeo;
  font-size: 1rem;
  margin : auto auto auto 1rem;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.19;
  letter-spacing: normal;
  text-align: left;
  color: #151515;
}
.textNumber{
  font-family: AppleSDGothicNeo;
  font-size: 1.25rem;
  margin : auto 1rem auto auto;
  font-weight: bold;
  text-align: right;
  color: #00462a;
}

.more{
    font-family: acumin-pro-wide, sans-serif;
    font-size: 0.75rem;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.33;
    letter-spacing: normal;
    text-align: left;
    color: #5f8276;
    margin : auto 0.5rem auto auto;
  }
  .rowBox{
    display : flex;
    flex-direction: row;
    height : 1.5rem;
    margin-bottom : 2.25rem
  }
  
  .link{
    margin: auto 0rem auto 0rem;
    padding: 0;
    overflow:auto;
    display : flex;
    flex-direction: row;
    text-decoration: none;
  
  }